'use client';

import { useEffect, useState } from 'react';
import { Input } from '@/src/components/ui/input';
import { Textarea } from '@/src/components/ui/textarea';
import { Label } from '@/src/components/ui/label';
import { Button } from '@/src/components/ui/button';
import {
  SubscriptionPackage,
  DurationType,
  ChargingMethod,
} from '@/src/lib/subscriptionPackages/subscriptionPackagesType';
import { getCompanyName } from '@/src/lib/rentalCompanies/getCompanyName';

interface Props {
  companyId: string;
  editingPackage: SubscriptionPackage | null;
  onSave: (data: Omit<SubscriptionPackage, 'id' | 'createdAt' | 'updatedAt'>) => void;
  onUpdate: (data: SubscriptionPackage) => void;
  onCancel: () => void;
}

export default function SubscriptionPackageForm({
  companyId,
  editingPackage,
  onSave,
  onUpdate,
  onCancel,
}: Props) {
  const initialForm: Omit<SubscriptionPackage, 'id' | 'createdAt' | 'updatedAt'> = {
    companyId,
    name: '',
    durationType: 'daily',
    kmLimit: null,
    chargingMethod: 'self',
    basePrice: 0,
    overageRate: null,
    note: '',
  };

  const [form, setForm] = useState(initialForm);
  const [message, setMessage] = useState('');
  const [companyName, setCompanyName] = useState('');

  useEffect(() => {
    const id = editingPackage?.companyId || companyId;
    if (id) {
      getCompanyName(id).then(setCompanyName);
    }
  }, [editingPackage, companyId]);

  useEffect(() => {
    if (editingPackage) {
      const { id, createdAt, updatedAt, ...rest } = editingPackage;
      setForm(rest);
    } else {
      setForm({ ...initialForm, companyId });
    }
  }, [editingPackage, companyId]);

  const handleChange = (field: keyof typeof form, value: any) => {
    setForm((prev) => ({
      ...prev,
      [field]: value === '' ? null : value,
    }));
  };

  const handleSubmit = () => {
    if (editingPackage) {
      onUpdate({ ...editingPackage, ...form });
      setMessage('✅ Package updated successfully!');
    } else {
      onSave(form);
      setForm({ ...initialForm, companyId });
      setMessage('✅ Package added successfully!');
    }

    setTimeout(() => setMessage(''), 3000);
  };

  return (
    <div className="bg-white shadow rounded-xl p-4 space-y-4">
      {message && <div className="text-green-600 font-semibold">{message}</div>}

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <Label>Company</Label>
          <p className="px-3 py-2 bg-gray-50 border rounded">{companyName || '...'}</p>
        </div>

        <div>
          <Label>Package Name</Label>
          <Input
            value={form.name}
            onChange={(e) => handleChange('name', e.target.value)}
          />
        </div>

        <div>
          <Label>Duration Type</Label>
          <select
            value={form.durationType}
            onChange={(e) =>
              handleChange('durationType', e.target.value as DurationType)
            }
            className="w-full border px-3 py-2 rounded"
          >
            <option value="daily">Daily</option>
            <option value="monthly">Monthly</option>
          </select>
        </div>

        <div>
          <Label>KM Limit (0 or blank = Unlimited)</Label>
          <Input
            type="number"
            value={form.kmLimit ?? ''}
            onChange={(e) => handleChange('kmLimit', e.target.value === '' ? null : Number(e.target.value))}
          />
        </div>

        <div>
          <Label>Charging Method</Label>
          <select
            value={form.chargingMethod}
            onChange={(e) =>
              handleChange('chargingMethod', e.target.value as ChargingMethod)
            }
            className="w-full border px-3 py-2 rounded"
          >
            <option value="self">Self-charging</option>
            <option value="swap">Battery swap</option>
          </select>
        </div>

        <div>
          <Label>Base Price (VND)</Label>
          <Input
            type="number"
            value={form.basePrice}
            onChange={(e) => handleChange('basePrice', Number(e.target.value))}
          />
        </div>

        <div>
          <Label>Overage Fee (VND/km)</Label>
          <Input
            type="number"
            value={form.overageRate ?? ''}
            onChange={(e) => handleChange('overageRate', e.target.value === '' ? null : Number(e.target.value))}
          />
        </div>
      </div>

      <div>
        <Label>Note</Label>
        <Textarea
          value={form.note || ''}
          onChange={(e) => handleChange('note', e.target.value)}
        />
      </div>

      <div className="flex gap-4 pt-2">
        <Button onClick={handleSubmit}>
          {editingPackage ? 'Update Package' : 'Create Package'}
        </Button>
        {editingPackage && (
          <Button variant="ghost" onClick={onCancel}>
            Cancel
          </Button>
        )}
      </div>
    </div>
  );
}