import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';
import { SubscriptionPackage } from '@/src/lib/subscriptionPackages/subscriptionPackagesType';

export function exportSubscriptionPackages(packages: SubscriptionPackage[]) {
  if (packages.length === 0) {
    alert('⚠️ No subscription packages to export.');
    return;
  }

  const data = packages.map((pkg) => ({
    'Package Name': pkg.name,
    'Duration Type': pkg.durationType === 'monthly' ? 'Monthly' : 'Daily',
    'KM Limit': pkg.kmLimit ?? 'Unlimited',
    'Charging Method': pkg.chargingMethod === 'swap' ? 'Battery Swap' : 'Self-charging',
    'Base Price (VND)': pkg.basePrice,
    'Overage Fee (VND/km)': pkg.overageRate ?? 'Not applied',
    'Note': pkg.note || '',
  }));

  const worksheet = XLSX.utils.json_to_sheet(data, { skipHeader: false });
  const workbook = XLSX.utils.book_new();

  XLSX.utils.book_append_sheet(workbook, worksheet, 'SubscriptionPackages');

  const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });

  const blob = new Blob([excelBuffer], {
    type:
      'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8',
  });

  saveAs(blob, 'subscription-packages.xlsx');
}
