'use client';

import { useEffect, useState } from 'react';
import { Input } from '@/src/components/ui/input';
import { Button } from '@/src/components/ui/button';
import { SubscriptionPackage } from '@/src/lib/subscriptionPackages/subscriptionPackagesType';

interface Props {
  packages: SubscriptionPackage[];
  setFiltered: (filtered: SubscriptionPackage[]) => void;
  onImport: () => void;
  onExport: () => void;
}

export default function SubscriptionPackageSearchFilter({
  packages,
  setFiltered,
  onImport,
  onExport,
}: Props) {
  const [searchTerm, setSearchTerm] = useState('');
  const [durationFilter, setDurationFilter] = useState('');
  const [chargingFilter, setChargingFilter] = useState('');

  useEffect(() => {
    let filtered = [...packages];

    if (searchTerm.trim()) {
      const keyword = searchTerm.toLowerCase();
      filtered = filtered.filter((p) => p.name.toLowerCase().includes(keyword));
    }

    if (durationFilter) {
      filtered = filtered.filter((p) => p.durationType === durationFilter);
    }

    if (chargingFilter) {
      filtered = filtered.filter((p) => p.chargingMethod === chargingFilter);
    }

    setFiltered(filtered);
  }, [searchTerm, durationFilter, chargingFilter, packages, setFiltered]);

  const handleClear = () => {
    setSearchTerm('');
    setDurationFilter('');
    setChargingFilter('');
    setFiltered(packages);
  };

  return (
    <div className="flex flex-col md:flex-row justify-between gap-4 mb-4 items-start md:items-center">
      <div className="flex flex-wrap gap-2 items-center">
        <Input
          placeholder="🔍 Search by name"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="w-48"
        />

        <select
          value={durationFilter}
          onChange={(e) => setDurationFilter(e.target.value as 'daily' | 'monthly' | '')}
          className="border px-3 py-2 rounded text-sm"
        >
          <option value="">All durations</option>
          <option value="daily">Daily</option>
          <option value="monthly">Monthly</option>
        </select>

        <select
          value={chargingFilter}
          onChange={(e) => setChargingFilter(e.target.value as 'self' | 'swap' | '')}
          className="border px-3 py-2 rounded text-sm"
        >
          <option value="">All charging</option>
          <option value="self">Self-charging</option>
          <option value="swap">Battery Swap</option>
        </select>

        <Button variant="ghost" onClick={handleClear} className="text-sm">
          Clear
        </Button>
      </div>

      <div className="flex gap-2">
        <Button variant="outline" onClick={onImport}>📥 Import</Button>
        <Button onClick={onExport}>📤 Export</Button>
      </div>
    </div>
  );
}
