import * as XLSX from 'xlsx';
import { Timestamp, addDoc, collection } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { SubscriptionPackage } from '@/src/lib/subscriptionPackages/subscriptionPackagesType';
import { validateImportRow, ImportRow } from '@/src/lib/subscriptionPackages/validateImportRow';

export async function importSubscriptionPackages(file: File, companyId: string): Promise<void> {
  const reader = new FileReader();

  return new Promise((resolve, reject) => {
    reader.onload = async (e) => {
      try {
        const data = new Uint8Array(e.target?.result as ArrayBuffer);
        const workbook = XLSX.read(data, { type: 'array' });
        const sheet = workbook.Sheets[workbook.SheetNames[0]];
        const rows: ImportRow[] = XLSX.utils.sheet_to_json(sheet);
        const now = Timestamp.now();

        const promises = rows.map(async (row, index) => {
          const error = validateImportRow(row);
          if (error) {
            console.warn(`⚠️ Row ${index + 2} skipped: ${error}`);
            return;
          }

          const pkg: Omit<SubscriptionPackage, 'id' | 'createdAt' | 'updatedAt'> = {
            companyId,
            name: row['Package Name'].trim(),
            durationType: row['Duration Type'].toLowerCase() === 'monthly' ? 'monthly' : 'daily',
            kmLimit:
              row['KM Limit'] === 'Unlimited' || row['KM Limit'] === ''
                ? null
                : Number(row['KM Limit']),
            chargingMethod: row['Charging Method'].toLowerCase().includes('swap') ? 'swap' : 'self',
            basePrice: Number(row['Base Price (VND)']),
            overageRate:
              row['Overage Fee (VND/km)'] === 'Not applied' || row['Overage Fee (VND/km)'] === ''
                ? null
                : Number(row['Overage Fee (VND/km)']),
            note: row['Note']?.trim() || '',
          };

          await addDoc(collection(db, 'subscriptionPackages'), {
            ...pkg,
            createdAt: now,
            updatedAt: now,
          });
        });

        await Promise.all(promises);
        resolve();
      } catch (err) {
        console.error('❌ Error importing subscription packages:', err);
        reject(err);
      }
    };

    reader.readAsArrayBuffer(file);
  });
}
