'use client';

import { useEffect, useState } from 'react';
import {
  collection,
  query,
  where,
  onSnapshot,
  addDoc,
  updateDoc,
  deleteDoc,
  doc,
  Timestamp,
} from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { SubscriptionPackage } from '@/src/lib/subscriptionPackages/subscriptionPackagesType';
import { getCompanyName } from '@/src/lib/rentalCompanies/getCompanyName';

export function useSubscriptionPackages(companyId: string) {
  const [packages, setPackages] = useState<SubscriptionPackage[]>([]);
  const [loading, setLoading] = useState(true);
  const [companyNameMap, setCompanyNameMap] = useState<Record<string, string>>({});

  useEffect(() => {
    if (!companyId) return;

    const q = query(
      collection(db, 'subscriptionPackages'),
      where('companyId', '==', companyId)
    );

    const unsubscribe = onSnapshot(q, async (snapshot) => {
      const data: SubscriptionPackage[] = [];
      const companyIds = new Set<string>();

      snapshot.forEach((docSnap) => {
        const pkg = {
          id: docSnap.id,
          ...docSnap.data(),
        } as SubscriptionPackage;

        data.push(pkg);
        companyIds.add(pkg.companyId);
      });

      setPackages(data);
      setLoading(false);

      const newMap: Record<string, string> = {};
      await Promise.all(
        Array.from(companyIds).map(async (id) => {
          const name = await getCompanyName(id);
          newMap[id] = name;
        })
      );
      setCompanyNameMap(newMap);
    });

    return () => unsubscribe();
  }, [companyId]);

  const addPackage = async (
    pkg: Omit<SubscriptionPackage, 'id' | 'createdAt' | 'updatedAt'>
  ) => {
    const now = Timestamp.now();
    await addDoc(collection(db, 'subscriptionPackages'), {
      ...pkg,
      createdAt: now,
      updatedAt: now,
    });
  };

  const updatePackage = async (pkg: SubscriptionPackage) => {
    if (!pkg.id) return;

    const ref = doc(db, 'subscriptionPackages', pkg.id);
    const {
      id,
      createdAt,
      updatedAt,
      ...cleaned
    } = pkg;

    await updateDoc(ref, {
      ...cleaned,
      updatedAt: Timestamp.now(),
    });
  };

  const deletePackage = async (id: string) => {
    await deleteDoc(doc(db, 'subscriptionPackages', id));
  };

  return {
    packages,
    loading,
    addPackage,
    updatePackage,
    deletePackage,
    companyNameMap,
  };
}
