// lib/subscriptionPackages/validateImportRow.ts
// Xữ lý file excel nhập xuất

export interface ImportRow {
    'Package Name': string;
    'Duration Type': string;         // Daily / Monthly
    'KM Limit': number | string;
    'Charging Method': string;       // Battery Swap / Self-charging
    'Base Price (VND)': number | string;
    'Overage Fee (VND/km)': number | string;
    'Note'?: string;
  }
  
  export function validateImportRow(row: ImportRow): string | null {
    if (!row['Package Name'] || typeof row['Package Name'] !== 'string') {
      return 'Missing or invalid "Package Name"';
    }
  
    const duration = row['Duration Type']?.toLowerCase();
    if (!['daily', 'monthly'].includes(duration)) {
      return 'Invalid "Duration Type", must be Daily or Monthly';
    }
  
    if (row['Base Price (VND)'] === '' || isNaN(Number(row['Base Price (VND)']))) {
      return 'Missing or invalid "Base Price (VND)"';
    }
  
    const charging = row['Charging Method']?.toLowerCase();
    if (!charging.includes('swap') && !charging.includes('self')) {
      return 'Invalid "Charging Method", must be Self-charging or Battery Swap';
    }
  
    return null; // ✅ Passed
  }
  