'use client';

import { useState, useMemo } from 'react';
import { SubscriptionPackage } from '@/src/lib/subscriptionPackages/subscriptionPackagesType';
import { Button } from '@/src/components/ui/button';
import { Input } from '@/src/components/ui/input';
import { ArrowUpDown } from 'lucide-react';

interface Props {
  packages: SubscriptionPackage[];
  companyNameMap: Record<string, string>;
  onEdit: (pkg: SubscriptionPackage) => void;
  onDelete: (id: string) => void;
}

export default function SubscriptionPackageTable({
  packages,
  companyNameMap,
  onEdit,
  onDelete,
}: Props) {
  const [searchTerm, setSearchTerm] = useState('');
  const [sortAsc, setSortAsc] = useState(true);
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10;

  const filteredPackages = useMemo(() => {
    const keyword = searchTerm.toLowerCase();
    const filtered = packages.filter((pkg) =>
      pkg.name.toLowerCase().includes(keyword)
    );
    return filtered.sort((a, b) => {
      const nameA = a.name.toLowerCase();
      const nameB = b.name.toLowerCase();
      return sortAsc ? nameA.localeCompare(nameB) : nameB.localeCompare(nameA);
    });
  }, [packages, searchTerm, sortAsc]);

  const paginated = useMemo(() => {
    const start = (currentPage - 1) * itemsPerPage;
    return filteredPackages.slice(start, start + itemsPerPage);
  }, [filteredPackages, currentPage]);

  const totalPages = Math.ceil(filteredPackages.length / itemsPerPage);

  const toggleSort = () => setSortAsc(!sortAsc);
  const changePage = (page: number) => {
    if (page >= 1 && page <= totalPages) setCurrentPage(page);
  };

  return (
    <div className="bg-white shadow rounded-xl p-4 mt-4 space-y-4">
      <div className="flex items-center justify-between">
        <h2 className="font-semibold text-lg">Subscription Packages</h2>
        <Input
          placeholder="Search by name..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="w-64"
        />
      </div>

      <div className="overflow-x-auto">
        <table className="min-w-full border border-gray-200 text-sm">
          <thead className="bg-gray-100">
            <tr>
              <th className="px-3 py-2 border cursor-pointer" onClick={toggleSort}>
                <div className="flex items-center gap-1">
                  Package Name <ArrowUpDown className="w-4 h-4" />
                </div>
              </th>
              <th className="px-3 py-2 border">Company</th>
              <th className="px-3 py-2 border">Duration</th>
              <th className="px-3 py-2 border">KM Limit</th>
              <th className="px-3 py-2 border">Charging</th>
              <th className="px-3 py-2 border">Base Price</th>
              <th className="px-3 py-2 border">Overage Rate</th>
              <th className="px-3 py-2 border">Note</th>
              <th className="px-3 py-2 border">Actions</th>
            </tr>
          </thead>
          <tbody>
            {paginated.length === 0 ? (
              <tr>
                <td colSpan={9} className="text-center py-4 text-gray-500">
                  No subscription packages found.
                </td>
              </tr>
            ) : (
              paginated.map((pkg) => (
                <tr key={pkg.id} className="border-t hover:bg-gray-50">
                  <td className="px-3 py-2 border">{pkg.name}</td>
                  <td className="px-3 py-2 border">
                    {companyNameMap[pkg.companyId] || pkg.companyId}
                  </td>
                  <td className="px-3 py-2 border capitalize">{pkg.durationType}</td>
                  <td className="px-3 py-2 border">
                    {pkg.kmLimit ? `${pkg.kmLimit.toLocaleString()} km` : 'Unlimited'}
                  </td>
                  <td className="px-3 py-2 border capitalize">
                    {pkg.chargingMethod === 'swap' ? 'Battery Swap' : 'Self-charging'}
                  </td>
                  <td className="px-3 py-2 border">{pkg.basePrice.toLocaleString()}₫</td>
                  <td className="px-3 py-2 border">
                    {pkg.overageRate ? `${pkg.overageRate.toLocaleString()}₫/km` : 'Not applied'}
                  </td>
                  <td className="px-3 py-2 border whitespace-pre-wrap w-[24rem] min-w-[300px]">
                    {pkg.note || '-'}
                  </td>
                  <td className="px-3 py-2 border whitespace-nowrap">
                    <div className="flex gap-2">
                      <Button variant="default" onClick={() => onEdit(pkg)}>
                        Edit
                      </Button>
                      <Button variant="danger" onClick={() => onDelete(pkg.id!)}>
                        Delete
                      </Button>
                    </div>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      {totalPages > 1 && (
        <div className="flex justify-center mt-4 gap-2 text-sm">
          <Button
            variant="outline"
            onClick={() => changePage(currentPage - 1)}
            disabled={currentPage === 1}
          >
            ⬅️ Prev
          </Button>
          {Array.from({ length: totalPages }, (_, i) => i + 1).map((page) => (
            <Button
              key={page}
              variant={page === currentPage ? 'default' : 'outline'}
              onClick={() => changePage(page)}
            >
              {page}
            </Button>
          ))}
          <Button
            variant="outline"
            onClick={() => changePage(currentPage + 1)}
            disabled={currentPage === totalPages}
          >
            Next ➡️
          </Button>
        </div>
      )}
    </div>
  );
}
