'use client';

import { useState, useEffect } from 'react';
import { useSubscriptionPackages } from '@/src/components/subscriptionPackage/useSubscriptionPackages';
import { SubscriptionPackage } from '@/src/lib/subscriptionPackages/subscriptionPackagesType';
import SubscriptionPackageSearchFilter from '@/src/components/subscriptionPackage/SubscriptionPackageSearchFilter';
import SubscriptionPackageForm from '@/src/components/subscriptionPackage/FormSubscriptionPackage';
import SubscriptionPackageTable from '@/src/components/subscriptionPackage/SubscriptionPackageTable';
import { importSubscriptionPackages } from '@/src/components/subscriptionPackage/importSubscriptionPackages';
import { exportSubscriptionPackages } from '@/src/components/subscriptionPackage/exportSubscriptionPackages';
import Header from '@/src/components/landingpage/Header';
import Footer from '@/src/components/landingpage/Footer';
import UserTopMenu from '@/src/components/landingpage/UserTopMenu';
import { getCompanyName } from '@/src/lib/rentalCompanies/getCompanyName';
import NotificationDialog from '@/src/components/ui/NotificationDialog';

const companyId = 'engkXQIeqS7QNJPWKPrw'; // Tạm thời cố định ID

export default function SubscriptionPackageManager() {
  const {
    packages,
    loading,
    addPackage,
    updatePackage,
    deletePackage,
    companyNameMap,
  } = useSubscriptionPackages(companyId);

  const [filtered, setFiltered] = useState<SubscriptionPackage[]>([]);
  const [editing, setEditing] = useState<SubscriptionPackage | null>(null);
  const [companyName, setCompanyName] = useState('');

  const [dialog, setDialog] = useState<{
    open: boolean;
    title: string;
    onConfirm?: () => void;
  }>({
    open: false,
    title: '',
  });

  useEffect(() => {
    getCompanyName(companyId).then(setCompanyName);
  }, []);

  const handleImport = async () => {
    const input = document.createElement('input');
    input.type = 'file';
    input.accept = '.xlsx';
    input.onchange = async (e: any) => {
      const file = e.target.files?.[0];
      if (!file) return;

      try {
        await importSubscriptionPackages(file, companyId);
        setDialog({ open: true, title: '✅ Imported successfully!' });
      } catch {
        setDialog({ open: true, title: '❌ Import failed.' });
      }
    };
    input.click();
  };

  const handleExport = () => {
    exportSubscriptionPackages(filtered.length > 0 ? filtered : packages);
  };

  const handleSave = async (pkg: Omit<SubscriptionPackage, 'id' | 'createdAt' | 'updatedAt'>) => {
    await addPackage(pkg);
  };

  const handleUpdate = async (pkg: SubscriptionPackage) => {
    await updatePackage(pkg);
    setEditing(null);
  };

  const handleDelete = async (id: string) => {
    setDialog({
      open: true,
      title: 'Delete this package?',
      onConfirm: async () => {
        await deletePackage(id);
        setDialog({ open: false, title: '' });
      },
    });
  };

  return (
    <div className="flex flex-col min-h-screen">
      <Header />
      <UserTopMenu />

      <div className="p-4 space-y-6">
        <h1 className="text-2xl font-semibold mb-4 border-b-2 pb-2">
          📦 Subscription Package Management
        </h1>

        <SubscriptionPackageForm
          companyId={companyId}
          editingPackage={editing}
          onSave={handleSave}
          onUpdate={handleUpdate}
          onCancel={() => setEditing(null)}
        />

        <SubscriptionPackageSearchFilter
          packages={packages}
          setFiltered={setFiltered}
          onImport={handleImport}
          onExport={handleExport}
        />

        <SubscriptionPackageTable
          packages={filtered.length > 0 ? filtered : packages}
          companyNameMap={companyNameMap}
          onEdit={setEditing}
          onDelete={handleDelete}
        />
      </div>

      <Footer />

      <NotificationDialog
        open={dialog.open}
        type={dialog.onConfirm ? 'confirm' : 'info'}
        title={dialog.title}
        onConfirm={dialog.onConfirm}
        onClose={() => setDialog({ open: false, title: '', onConfirm: undefined })}
      />
    </div>
  );
}
